<template>
<div >
  <transition name="fade">
    <Loading2 v-if="ishow"></Loading2>
  </transition>
  <Scroll  :pullup="pullup" @pulldown="loadData"  ref="scroll" >
  <div class="findIndex">
    <!-- 轮播图 -->
    <div class="banner">
      <slide :bannerImg="bannerImg" :inv="invTime"></slide>
    </div>
    <!-- 横向滑动菜单 -->
    <div class="tab">
      <myTab ></myTab>
    </div>
    <!-- 歌单推荐 -->
    <div class="songlist">
      <div class="text" >
      <span @click="$router.push('/songlistIndex')">为你精挑细选 ></span>
      <el-button size="mini" round @click="$router.push('/songlistIndex')">查看更多</el-button>
      </div>
      <songList ></songList>
    </div>
    <!-- 风格推荐 -->
    <div class="newsong" >
      <div class="text">
        <span @click="$router.push('/newsongIndex')">最新音乐 ></span>
        <el-button size="mini" round @click="$router.push('/newsongIndex')">查看更多</el-button>
      </div >
      <newsong></newsong>
    </div>
    <!-- 电台音乐 -->
    <div class="dj">
      <div class="text">
        <span @click="$router.push('/djIndex')">嘿！你在听我说嘛？</span>
        <el-button size="mini" round @click="$router.push('/djIndex')">查看更多</el-button>
      </div>
     
      <Dj></Dj>
    </div>
    
  </div>
  </Scroll>
</div>
</template>

<script>
import Scroll from '../common/Scroll.vue'
// 引入轮播图组件
import slide from './component/Slide.vue'
import myTab from './component/MenuList.vue'
import songList from './component/SongList.vue'
import newsong from './component/NewSong.vue'
import Dj from './component/Dj.vue'
import Loading2 from '../common/Loading2'
import TroggerVue from '../common/Trogger.vue'
export default {
    components:{
        Scroll,
        slide,
        myTab,
        songList,
        newsong,
        Dj,
        Loading2,
    },
  data() {
    return {
      invTime:3000,
      rdlist: [],
      pullup:true,
      ishow:true,
      bannerImg:[]
    }

  },
 async created(){
     const {data:res}=await this.$http.get('/banner?type=2')
    //  console.log(res.banners)
    res.banners.forEach(item => {
     this.bannerImg.push(item.pic) 
    });
   this.loadData()

  },
  mounted(){
    this.$nextTick(()=>{
      this.ishow=false
    })
  },
 activated(){
   this.loadData()
 },
  methods:{
    loadData() {
    this.$nextTick(()=>{
      this.$refs.scroll.refresh()
    })
    
    },
     
  }
};
</script>

<style lang='less' scoped>
.findIndex{
  width: 90%;
  margin-left: 5%;
  overflow: hidden;
}
.banner {
 
  width: 100%;
  margin-top: 50px;
  height: 24vh;
  background-color: white;
  list-style: none;
  position: relative;
 
}
.tab{
  margin-top: 20px;
}
.songlist{
  width: 100%;
  margin-top: 20px;
  
 
}
.text{
    
    margin-bottom: 15px;
    .el-button{
    float: right;
    color: #666;
  }
    span{
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  }
.newsong{
  width: 100%;
   margin-top: 20px;
   
}
.dj{
  width: 100%;
   margin-top: 20px;
  
}
</style>