<template>
<!-- 横向菜单 -->
  <div class="my-tabs">
    <div class="tabs" ref="tabsWrapper">
      <ul ref="tab">
          <div class="tab-item" v-for="(item, index) in menulist" :key="index" @click="toPage(item)">
              <div class="img-box">
                <i :class="['iconfont', item.src]"></i>
                  <!-- <img :src="item.src" alt="" > -->
              </div>
                <p>{{item.title}}</p>
          </div>
        
      </ul>
    </div>
  </div>
</template>
 
<script>
// 横向滑动菜单
import BScroll from 'better-scroll'
 
export default {
  props:{
    id:{
      type:Number,
      default:null
    }
  },
  data () {
    return {
      menulist:[
                {
                    src:'icon-gedan1',
                    title:'歌单',
                    path:'/songlistIndex'
                },
                {
                    src:'icon-kefu',
                    title:'最新歌曲',
                    path:'/newsongIndex'
                },
                {
                    src:'icon-paihangbang',
                    title:'排行',
                    path:'/rank'
                },
                 {
                    src:'icon-diantai',
                    title:'电台',
                    path:'/find'
                },
                {
                    src:'icon-meirituijian',
                    title:'推荐',
                    path:'/find'
                },
               
                {
                    src:'icon-zhibobofangshexiangjitianxianxianxing',
                    title:'放映室',
                    path:'/find'
                },
            ],
     
      tabWidth: 90
    }
  },
  mounted () {
    this.$nextTick(() => {
      this._initMenu()
    })
  },
  methods: {
   
    _initMenu () {
      let tabsWidth = this.tabWidth
      let width = this.menulist.length * tabsWidth
      this.$refs.tab.style.width = `${width}px`
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.tabsWrapper, {
            scrollX: true,
            eventPassthrough: 'vertical'
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    toPage(e){
      console.log(e)
      this.$router.push(e.path)
      // this.$emit('toPage')
    }
  
  },
  
}
</script>
 
<style lang="less" scoped>
 .my-tabs{
     position: relative;
     width: 100%;
    //  height: 70px;
     background-color: #fff;
     .tabs{

         overflow: hidden;
         width: 100%;
         height: 80px;
         box-sizing :border-box;
        white-space:nowrap;
        
        .tab-item{
          width: 63px;
           float: left;
           text-align: center;
           margin-right: 5%;
          ul{
            width: 100%;
          }
            .img-box{
                width: 42px;
                height: 42px;
                background-color: #de464e;
                 box-shadow: 2px 0 1px rgba(0,0,0,0.3);
                border-radius: 50%;
                padding: 8px;
               
                i{
                    // width: 100%;
                    // height: 100%;
                    font-size:36px;
                    color: #fff;
                     text-align: center;
                }
             .icon-meirituijian {
                  font-size: 44px;
                 margin-bottom: 20px;
                }
            }
            p{
                font-size: 12px;
            }
        }
     }
 }
</style>
