<template>
    <div class="dj" ref="dj" >
        <ul class="listcontent" ref="content">
            <li class="listitem" v-for="(item,index) in djList" :key="index" ref="listitem" >
                <img :src="item.picUrl" alt="">
                <p>{{item.name}}</p>
            </li>
        </ul>
       
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    data(){
        return{
            djList:[],
            listWidth:130,
           
        }
    },
   async created(){
        // this.getNewSong()
         const {data:res}=await this.$http.get('/personalized/djprogram')
         console.log(res)
           this.djList=res.result
        this.$nextTick(()=>{
            this._initScroll()
        })
    },
    methods:{
       _initScroll(){
        
        let listWidth=this.listWidth
        let width=this.djList.length*listWidth
        this.$refs.content.style.width=`${width}px`
     
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.dj, {
            scrollX: true,
            eventPassthrough: "vertical"
          });
        } else {
          this.scroll.refresh();
        }
      });
       },
       
    }
}
</script>

<style lang='less' scoped>
.dj{
    
    width: 100%;
    touch-action: none;
    .listcontent{
        height: 170px;
        width: 100%;
        box-sizing: border-box;
        overflow: hidden;
        
        .listitem{
            width: 100px;
            height: 160px;
            float: left;   
            // margin: 0 4px;
            text-align: center;
            margin: 10px;
            img{
                width: 100px;
                height: 100px;
            }
            p{
                font-size: 14px;
            }
        }
    }
}
</style>