<template>
  <!-- 歌单页面 -->
    <div class="rds" ref="rdsWrapper">
      <ul ref="rdlist">
        <li class="rdlist-item"
          v-for="(item,index) in rdlist"
          :key="index"
          @click="toSongList(item.id)"
         >
            <img :src="item.img" alt />
          
          <p>{{item.title}}</p>
        </li>
      </ul>
    </div>
  
</template>


<script>
import BScroll from "better-scroll";
export default {
  data() {
    return {
      rdlist: [],
     
      listWidth: 130,
      obj:{
        id:'',
        title:'',
        img:''
      }
    };
  },
 async created() {
    
     const { data: res } = await this.$http.get("/homepage/block/page");
     console.log(res.data.blocks[0].creatives)
      res.data.blocks[0].creatives.forEach((item) => {
        let obj={}
        obj.id=item.creativeId
        obj.title=item.uiElement.mainTitle.title
        obj.img=item.uiElement.image.imageUrl
        this.rdlist.push(obj);
      });
       console.log(this.rdlist)
    this.$nextTick(() => {
      this._initList();
      
    });
    
  },
  methods: {
    _initList() {
    let listWidth=this.listWidth
      let width = this.rdlist.length * listWidth;
      this.$refs.rdlist.style.width = `${width}px`;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.rdsWrapper, {
            scrollX: true,
            eventPassthrough: "vertical",
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
   
    toSongList(e){
      // console.log(e)
      this.$router.push({path:'/listdetail',query:{
        id:e
        }})
    }
  },
};
</script>


<style lang='less' scoped>

  .rds {
    // width: 100%;
    height: 170px;
   
    touch-action: none;
    box-sizing: border-box;
    .rdlist-item {
     overflow: hidden;
      width: 100px;
      height: 160px;
      float: left;
      text-align: center;
      margin:10px;
      
      img {
        width: 100px;
        height: 100px;
      }
      p {
        font-size: 14px;
      }
    }
  }

</style>