<template>
  <div>
      <div :class="{'mask':isShow}" @click="hideMask"></div>
    <el-container>
    <Trogger :isShow='isShow' ></Trogger>
      <el-container>
        <el-header>
          <div class="left-icon">
            <i class="iconfont icon-zhankai" @click="showLeftNav"></i>
          </div>
          <div class="nav-list">
            <ul >
              <li v-for="item in navlist" :key="item.id" @click="switchTo(item)" class="navlist-text" :class="{active:currentIndex===item.id}" >
                <span>{{item.name}}</span>
              </li>
              
            </ul>
          </div>
          <div class="right-icon">
            <i class="el-icon-search"></i>
          </div>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>


<script>
import BScroll from 'better-scroll'
import Trogger from '@/components/common/Trogger.vue'


export default {
 
 components:{
  Trogger
 },
  data() {
    return {
      isShow: false,
      navlist:[
        {
          id:0,
          name:'我的',
          path:'/mine'
        },
        {
          id:1,
          name:'发现',
          path:'/find'
        },
        {
          id:2,
          name:'云村',
          path:'/clouds'
        },
        {
          id:3,
          name:'视频',
          path:'/video'
        },
      ],
      currentIndex:1,
      show:false,
      
    };
  },
  methods: {
 
    showLeftNav() {
      this.isShow = !this.isShow;
      
    },
   
  hideMask(){
    this.isShow=false
  },

   switchTo(e){
    //  console.log(path)
    this.$router.push(e.path)
    this.currentIndex=e.id
    console.log(this.currentIndex)
   },
  }
};
</script>

<style lang="less" scoped>
.mask{
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 100;
  background-color: #333;
  opacity: 0.5;
}

.el-header {
  width: 100%;
  background-color: #fff;
  position: fixed;
  z-index: 99;
 
  padding-right: 10%;
  padding-top: 20px;
  margin: 0;
  overflow: hidden;
  
  .left-icon {
    width: 20px;
    height: 20px;
    float: left;
    margin-top:3px;
  }
  .right-icon{
    width: 20px;
    height: 20px;
    float: right;
   margin-top:3px;
  }
  .nav-list{
    float: left;
    width: 66%;
    margin-left: 12%;
    
  }
  .nav-list ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    

  }
 .active{
  font-weight: bolder;
 font-size: 18px;
}
}
</style>